/* General styles for project cards */

.marquee {
  overflow: hidden;
  position: relative;
  width: 100%;
}

.marquee-content {
  display: flex;
  width: max-content;
}

.marquee-item {
  flex: 0 0 auto;
  margin: 0 15px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.card-img-container {
  overflow: hidden;
  height: 200px;
  position: relative;
}

.project-img {
  width: 100%;
  height: auto;
  transition: transform 6.5s ease;
}

.project-card:hover .project-img {
  transform: translateY(calc(200px - 100%));
  transition: transform 15s ease;
}

.project-card {
  border-radius: 15px;
  overflow: hidden;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease;
  background-color: #6b53ff;
  color: #fff;
}

#projects {
  padding-bottom: 50px;
}

.card-body {
  padding: 15px;
}

.card-title {
  font-size: 1.5rem;
  font-weight: bold;
}

.card-text {
  font-size: 1rem;
  color: hsla(0, 0%, 100%, .8);
}

.dark-mode #projects {
  background-color: #3a3a3a;
  color: #fff;
}

/* Desktop-specific styles */

@media (min-width: 768px) {
  .marquee-container {
    display: block;
  }
  .swiper-container {
    display: none;
  }
  .marquee-content {
    animation: marquee 30s linear infinite;
  }
  @keyframes marquee {
    0% {
      transform: translateX(0%);
    }
    100% {
      transform: translateX(-50%);
    }
  }
}

/* Mobile-specific styles */

@media (max-width: 768px) {
  .marquee-container {
    display: none;
  }
  .swiper-container {
    display: block;
  }
  .marquee-item {
    width: 300px;
  }
}
